import Recorder from './Recorder.js'
export default class Record {
  startRecord (param) {
    let self = this
    try {
      Recorder.get(rec => {
        self.recorder = rec
        self.recorder.start()
      })
    } catch (e) {
      param.error('record failed!' + e)
    }
  }

  stopRecord (param) {
    let self = this
    try {
      let blobData = self.recorder.getBlob()
      param.success(blobData)
    } catch (e) {
      alert('record stop failed!' + e)
    }
  }

  play (audio) {
    let self = this
    self.recorder.play(audio)
  }
}
