<template>
  <div class="booth">
    <div class="video">
      <video v-show="showCam" ref="video" :width="width" :height="height"></video>
      <canvas v-show="false" ref="canvas" :width="width" :height="height"></canvas>
    </div>
    <div class="wrap">
      <a class="button rollover" @click="start_cam">
        <span class="roll-text">{{ BtnMsg }}</span>
        <span class="roll-text">{{ BtnMsgNext }}</span>
      </a>
    </div>

    <img ref="img" src="">
  </div>
</template>

<script>
export default {
  name: 'Camera',
  data () {
    return {
      BtnMsg: 'Start',
      BtnMsgNext: 'Let‘s Go!',
      width: 400,
      height: 300,
      stream: 0,
      showCam: false
    }
  },
  methods: {
    take_shot () {
      // 绘制canvas图形
      let canvas = this.$refs.canvas
      canvas.getContext('2d').drawImage(this.$refs.video, 0, 0, this.width, this.height)

      this.stop_cam()

      // 把canvas图像转为img图片
      this.$refs.img.src = canvas.toDataURL('image/jpeg')
      let file = this.$refs.img.src
      let fileBase64 = file.split(',')[1]
      this.$emit('set_image_data', fileBase64)
    },
    start_cam () {
      if (this.BtnMsg === 'Start') {
        this.BtnMsg = 'Snap a Shot'
        this.BtnMsgNext = 'Smile'
        navigator.mediaDevices.getUserMedia({
          video: {
            width: this.width,
            height: this.height
          },
          audio: false
        }).then(this.get_media_success)
      } else if (this.BtnMsg === 'Snap a Shot') {
        this.BtnMsg = 'nice photo'
        this.BtnMsgNext = 'nice photo'
        this.take_shot()
      }
    },
    stop_cam () {
      this.stream.getTracks().forEach(track => track.stop())
      this.showCam = false
    },
    get_media_success (stream) {
      this.showCam = true
      this.$refs.video.srcObject = stream
      this.$refs.video.play()
      this.stream = stream
    }
  }
}
</script>
