<template>
  <div class="record">
    <h1>Click following button to record voice:</h1>
    <a class="button rollover" @click="startRecord">
      <span class="roll-text">录音</span>
      <span class="roll-text">Start</span>
    </a>
    <a class="button rollover" @click="stopRecord">
      <span class="roll-text">停止</span>
      <span class="roll-text">Stop</span>
    </a>
    <a class="button rollover" @click="play">
      <span class="roll-text">播放</span>
      <span class="roll-text">Play</span>
    </a>
    <a class="button rollover">
      <span class="roll-text">{{ score }}</span>
      <span class="roll-text">{{ score }}</span>
    </a>
    <div class="record-play" v-show="isFinished">
      <h2>Current voice player is:</h2>
      <audio controls autoplay></audio>
    </div>
  </div>
</template>

<script>
import Record from '../assets/record-sdk.js'
export default {
  name: 'Record',
  data () {
    return {
      isFinished: false,
      audio: '',
      recorder: new Record(),
      score: '发音分数'
    }
  },
  methods: {
    startRecord: function () {
      this.isFinished = false
      this.recorder.startRecord({
        error: e => {
          alert('Recording error!' + e)
        }
      })
    },
    stopRecord: function () {
      let r = Math.round(Math.random() * 30) + 70
      this.score = '分数:' + String(r)
      this.isFinished = false
      let vm = this
      this.recorder.stopRecord({
        success: res => {
          // 此处可以获取音频源文件(res)，用于上传等操作
          let reader = new FileReader()
          reader.readAsDataURL(res)
          reader.onloadend = function () {
            let file = reader.result
            let fileBase64 = file.split(',')[1]
            vm.$emit('set_speech_data', fileBase64)
            vm.$emit('record_done')
          }
        },
        error: () => {

        }
      })
    },
    play: function () {
      let self = this
      self.isFinished = true
      self.audio = document.querySelector('audio')
      self.recorder.play(self.audio)
    },
    test () {
      this.$emit('test-func')
    }
  }
}
</script>

