<template>
  <div>
    <el-tabs v-model="activeName" @tab-click="change_tab">
      <el-tab-pane label="语音" name="first">
        <recorder
          @record_done="display_upload_button"
          @set_speech_data="set_speech_data"
        />
      </el-tab-pane>
      <el-tab-pane label="文字" name="second">
        <input type="text" v-model="inputText" @input="display_upload_button">
      </el-tab-pane>
    </el-tabs>
    <a class="button rollover" v-if="uploadVisible" @click="upload">
      <span class="roll-text">上传</span>
      <span class="roll-text">upload</span>
    </a>
    <video-player ref="videoright"/>
    <video-player ref="videowrong"/>
    <camera
      @set_image_data="set_image_data"
    />
  </div>
</template>

<script>
import Recorder from './components/Recorder.vue'
import Camera from './components/Camera.vue'
import VideoPlayer from './components/VideoPlayer.vue'

export default {
  name: 'app',
  components: {
    Camera,
    Recorder,
    VideoPlayer
  },
  data () {
    return {
      uploadVisible: false,
      imageData: false,
      speechData: false,
      inputText: '',
      activeName: 'first'
    }
  },
  methods: {
    video_gen (videoURL) {
      this.$refs.video.video_gen(videoURL)
    },
    play_video () {
      this.$refs.video.play()
    },
    display_upload_button () {
      this.uploadVisible = true
    },
    set_speech_data (speechData) {
      this.speechData = speechData
    },
    set_image_data (imageData) {
      this.imageData = imageData
    },
    upload () {
      let postData = {}
      if (this.activeName === 'first') {
        postData['speech_base64_data'] = this.speechData
      } else if (this.activeName === 'second') {
        postData['text_data'] = this.inputText
      }
      if (this.imageData) {
        postData['img_base64_data'] = this.imageData
      }
      this.$ajax.post('/', postData).then(res => {
        this.$refs.videoright.set_src(res.data.video_generated)
        this.$refs.videowrong.set_src(res.data.video_generated2)
      })
    },
    change_tab () {
      this.uploadVisible = false
      this.inputText = ''
    }
  }
}
</script>

